.App {
  text-align: center;
}
.table_blur {
  background: #f5ffff;
  border-collapse: collapse;
  text-align: left;
}
.table_blur th {
  border-top: 1px solid #777777;
  border-bottom: 1px solid #777777;
  -webkit-box-shadow: inset 0 1px 0 #999999, inset 0 -1px 0 #999999;
          box-shadow: inset 0 1px 0 #999999, inset 0 -1px 0 #999999;
  background: -webkit-gradient(linear, left top, left bottom, from(#9595b6), to(#5a567f));
  background: -webkit-linear-gradient(#9595b6, #5a567f);
  background: -o-linear-gradient(#9595b6, #5a567f);
  background: linear-gradient(#9595b6, #5a567f);
  color: white;
  padding: 10px 15px;
  position: relative;
}
.table_blur th:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 25%;
  height: 25%;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), to(rgba(255,255,255,.08)));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0), rgba(255,255,255,.08));
  background: -o-linear-gradient(rgba(255, 255, 255, 0), rgba(255,255,255,.08));
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255,255,255,.08));
}
.table_blur th:hover {
  cursor: pointer;
}
.table_blur th:first-child:hover {
  cursor: default !important;
}
.table_blur tr:nth-child(odd) {
  background: #ebf3f9;
}
.table_blur th:first-child {
  border-left: 1px solid #777777;
  border-bottom:  1px solid #777777;
  -webkit-box-shadow: inset 1px 1px 0 #999999, inset 0 -1px 0 #999999;
          box-shadow: inset 1px 1px 0 #999999, inset 0 -1px 0 #999999;
}
.table_blur th:last-child {
  border-right: 1px solid #777777;
  border-bottom:  1px solid #777777;
  -webkit-box-shadow: inset -1px 1px 0 #999999, inset 0 -1px 0 #999999;
          box-shadow: inset -1px 1px 0 #999999, inset 0 -1px 0 #999999;
}
.table_blur td {
  border: 1px solid #e3eef7;
  padding: 10px 15px;
  position: relative;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.table_blur tbody:hover tr:hover td {
  background: rgba(255, 191, 255, 0.6);
}
.red{color: red !important;}
.blue{color: blue !important;}


